import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import TableBody from "@mui/material/TableBody";

const dispatchedEvents = [];

const SrsMuiTableBody = function (props, ref) {
  return (
    <TableBody {...props} theme={props.muiTheme}>
      <slot />
    </TableBody>
  );
};

const styles = `
  /* Custom styles for the MuiTableBody component */
`;

const props = [
  {
    name: "content",
    defaultValue: null,
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to MuiTableBody if needed
];

const slots = [
  {
    name: "default",
    allowed: ["srs-mui-table-row"],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiTableBody),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
